import { useTheme } from '@emotion/react';
import { useState, useEffect } from 'react';
import { useNavigate, useParams, Link } from 'react-router-dom';
import { LoadingButton } from '@mui/lab';
import {
  Card, CardHeader, CardContent, Button,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Divider,
  InputLabel, Select, MenuItem,
  Grid, TextField, Radio, FormControl
}
  from '@mui/material';
import { Box, styled } from '@mui/system';
import { Formik } from 'formik';
import useAuth from 'app/hooks/useAuth';

import axios from 'axios';
import * as Yup from 'yup';
import swal from 'sweetalert';
import { Breadcrumb } from "app/components";

const FlexBox = styled(Box)(() => ({ display: 'flex', alignItems: 'center' }));

const JustifyBox = styled(FlexBox)(() => ({ justifyContent: 'center' }));

const ContentBox = styled(JustifyBox)(() => ({
  height: '100%',
  padding: '32px',
  background: 'rgba(0, 0, 0, 0.01)',
}));

const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));

const TaxCategoryTable = styled(Table)(() => ({
  minWidth: 400,
  whiteSpace: 'pre',
  '& small': {
    width: 50,
    height: 15,
    borderRadius: 500,
    boxShadow: '0 0 2px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.24)',
  },
  '& td': { borderBottom: 'none' },
  '& td:first-of-type': { paddingLeft: '16px !important' },
}));

// inital login credentials
const initialValues = {
  product_name: '',
  description: '',
  category_id: '',
  brand_id: '',
  unit_packing: '',
  expiry_date: '',
  available_qty: '',
  hsn_sac_code: '',
  batch_no: '',
  mrp: '',
  product_image: '',
  min_qty: '',
  min_qty_discount: '',
  min_qty_bonus_deal: '',
  min_qty_trade_rate: '',
  min_qty_net_rate: '',
  min_qty_margin_percentage: '',
  status: 'pending',
  tax_category_id: ''
};

// form field validation schema
const validationSchema = Yup.object().shape({
  product_name: Yup.string()
    .min(2, 'Product Name must be 10 digit length')
    .required('Product Name is required!'),
  category_id: Yup.string()
    .required('Category is required!'),
  brand_id: Yup.string()
    .required('Brand is required!'),
  expiry_date: Yup.string()
    .required('Expiry Date is required!'),
  available_qty: Yup.number()
    .required('Available Quantity is required!'),
  hsn_sac_code: Yup.string()
    .required('HSN/SAC is required!'),
  mrp: Yup.number()
    .required('MRP is required!'),
  min_qty: Yup.number()
    .required('Quantity is required!'),
  min_qty_discount: Yup.number()
    .required('Discount (%) is required!'),
  min_qty_bonus_deal: Yup.string()
    .required('Bonus (Deal) is required!'),
  min_qty_trade_rate: Yup.number()
    .required('Trade Rate is required!'),
  tax_category_id: Yup.number()
    .required('Tax Category is required!'),
});

const AddMedicinesStock = () => {
  const { row_id } = useParams();
  const theme = useTheme();
  const { register } = useAuth();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [categories, setCategory] = useState(false);
  const [brands, setBrands] = useState(false);
  const [tax_categories, setTaxCategories] = useState(false);
  const [product, setProduct] = useState(initialValues);
  const [formData, setFormData] = useState(initialValues);

  useEffect(() => {
    fetchData();
  }, []);

  /**
    * Fetching data from server.
    */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/client/get-categories')
          .then(response => {
            if (response.data.response_code === 200) {
              setCategory(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
        axios.post('/api/client/get-brands')
          .then(response => {
            if (response.data.response_code === 200) {
              setBrands(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });

        axios.post('/api/client/get-tax-categories')
          .then(response => {
            if (response.data.response_code === 200) {
              setTaxCategories(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
        axios.post('/api/client/get-medicine', {
          row_id: row_id
        })
          .then(response => {
            if (response.data.response_code === 200) {
              setProduct(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  const handleChange = (e) => {
    console.log(e.target.name + '-' + e.target.value);
    setFormData({ ...formData, [e.target.name]: e.target.value });
    console.log(formData);
  };

  const changeHandler = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.files[0] });
  };

  const handleFormSubmit = (values, errors) => {
    setLoading(true);
    try {
      axios.get('/sanctum/csrf-cookie').then(response => {
        const uploadFormData = new FormData();
        for (let iKey in values) {
          if (iKey === 'product_image') {
            uploadFormData.append(iKey, formData[iKey])
          } else {
            if (values[iKey]) {
              uploadFormData.append(iKey, values[iKey])
            } else {
              uploadFormData.append(iKey, '')
            }
          }
        }
        uploadFormData.append('row_id', row_id)
        const config = { headers: { "Content-Type": "multipart/form-data" } };
        axios.post('/api/client/update-medicine-stock', uploadFormData, config)
          .then(response => {
            if (response.data.response_code === 200) {
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
              navigate('/dashboard/medicine/list');
            } else {
              errors.setErrors(response.data.errors);
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  };

  return (
    <Container className="add-medicine">
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "Edit Medicine" }]} />
      </Box>
      <Card className="card" >
        <CardHeader
          title="Edit Medicine"
          titleTypographyProps={{
            variant: "h4",
            fontSize: '16px',
            fontWeight: '600',
            textTransform: 'uppercase',
            textAlign: 'center'
          }}
          action={
            <>
              <Button title="Back" component={Link}
                to={`/dashboard/medicine/list`}
                color="inherit" variant="outlined" size="small" sx={{ m: .5 }}>
                Back
              </Button>
            </>
          }
        />
        <Divider />
        <CardContent>
          <Formik
            onSubmit={(values, errors) => {
              handleFormSubmit(values, errors);
            }
            }
            // initialValues={product}
            // validationSchema={validationSchema}
            initialValues={product}
            enableReinitialize={true}
            validationSchema={validationSchema}
          >
            {({ values, errors, touched, handleChange, handleBlur, handleSubmit }) => (
              <form onSubmit={handleSubmit}>
                <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="product_name" className='form-label'>
                      Product Name
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="text"
                      name="product_name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.product_name}
                      onChange={handleChange}
                      helperText={touched.product_name && errors.product_name}
                      error={Boolean(errors.product_name && touched.product_name)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="category_id" className='form-label'>
                      Category
                    </InputLabel>
                    <FormControl fullWidth
                      sx={{ mb: 3 }}
                      size="small"
                    >
                      <Select
                        labelId="status-label"
                        id="category_id"
                        name="category_id"
                        onChange={handleChange}
                        variant="outlined"
                        onBlur={handleBlur}
                        value={values.category_id}
                        error={Boolean(errors.category_id && touched.category_id)}
                      >
                        <MenuItem value="" key="sg">Select Category</MenuItem>
                        {categories && categories.map((row) =>
                          <MenuItem value={row.id} key={row.id}>{row.name}</MenuItem>
                        )}
                      </Select>
                    </FormControl>
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="brand_id" className='form-label'>Brand</InputLabel>
                    <FormControl fullWidth
                      sx={{ mb: 3 }}
                      size="small"
                    >
                      <Select
                        labelId="status-label"
                        id="brand_id"
                        name="brand_id"
                        onChange={handleChange}
                        variant="outlined"
                        onBlur={handleBlur}
                        value={values.brand_id}
                        error={Boolean(errors.brand_id && touched.brand_id)}
                      >
                        <MenuItem value="" key="sg">Select Brand</MenuItem>
                        {brands && brands.map((row) =>
                          <MenuItem value={row.id} key={row.id}>{row.name}</MenuItem>
                        )}
                      </Select>
                    </FormControl>
                    <label htmlFor="unit_packing" className='form-label'>Unit Packing</label>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="unit_packing"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.unit_packing}
                      onChange={handleChange}
                      helperText={touched.unit_packing && errors.unit_packing}
                      error={Boolean(errors.unit_packing && touched.unit_packing)}
                      sx={{ mb: 3 }}
                    />

                  </Grid>
                  <Grid item sm={6} xs={12} lg={6} >
                    <label htmlFor="description" className='form-label'>Description</label>
                    <br />
                    <TextField
                      fullWidth
                      multiline={true}
                      minRows={6}
                      size="small"
                      type="text"
                      name="description"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.description || ''}
                      onChange={handleChange}
                      // helperText={touched.description && errors.description}
                      // error={Boolean(errors.description && touched.description)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="expiry_date" className='form-label'>Expiry Date</InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="date"
                      name="expiry_date"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.expiry_date}
                      onChange={handleChange}
                      helperText={touched.expiry_date && errors.expiry_date}
                      error={Boolean(errors.expiry_date && touched.expiry_date)}
                      sx={{ mb: 3 }}

                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="available_qty" className='form-label'>Available Quantity</InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="available_qty"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.available_qty}
                      onChange={handleChange}
                      helperText={touched.available_qty && errors.available_qty}
                      error={Boolean(errors.available_qty && touched.available_qty)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="hsn_sac_code" className='form-label'>HSN/SAC Code</InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="text"
                      name="hsn_sac_code"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.hsn_sac_code}
                      onChange={handleChange}
                      helperText={touched.hsn_sac_code && errors.hsn_sac_code}
                      error={Boolean(errors.hsn_sac_code && touched.hsn_sac_code)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <label htmlFor="batch_no" className='form-label'>Batch No.</label>
                    <TextField
                      fullWidth
                      size="small"
                      type="text"
                      name="batch_no"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.batch_no}
                      onChange={handleChange}
                      helperText={touched.batch_no && errors.batch_no}
                      error={Boolean(errors.batch_no && touched.batch_no)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <InputLabel required htmlFor="mrp" className='form-label'>
                      MRP
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="mrp"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.mrp || ''}
                      onChange={handleChange}
                      helperText={touched.mrp && errors.mrp}
                      error={Boolean(errors.mrp && touched.mrp)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>

                  </Grid>
                </Grid>
                <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                  <Grid item sm={6} xs={12} lg={2}>
                    <InputLabel required htmlFor="min_qty" className='form-label'>
                      Quantity
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="min_qty"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty}
                      onChange={handleChange}
                      helperText={touched.min_qty && errors.min_qty}
                      error={Boolean(errors.min_qty && touched.min_qty)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12} lg={2}>
                    <InputLabel required htmlFor="min_qty_discount" className='form-label'>
                      Discount (%)
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="min_qty_discount"
                      // label="Name"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty_discount}
                      onChange={handleChange}
                      helperText={touched.min_qty_discount && errors.min_qty_discount}
                      error={Boolean(errors.min_qty_discount && touched.min_qty_discount)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12} lg={2}>
                    <InputLabel required htmlFor="min_qty_bonus_deal" className='form-label'>
                      Bonus (Deal)
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="text"
                      name="min_qty_bonus_deal"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty_bonus_deal || ''}
                      onChange={handleChange}
                      helperText={touched.min_qty_bonus_deal && errors.min_qty_bonus_deal}
                      error={Boolean(errors.min_qty_bonus_deal && touched.min_qty_bonus_deal)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12} lg={2}>
                    <InputLabel required htmlFor="min_qty_trade_rate" className='form-label'>
                      Trade Rate
                    </InputLabel>
                    <TextField
                      fullWidth
                      size="small"
                      type="number"
                      name="min_qty_trade_rate"
                      variant="outlined"
                      onBlur={handleBlur}
                      value={values.min_qty_trade_rate}
                      onChange={handleChange}
                      helperText={touched.min_qty_trade_rate && errors.min_qty_trade_rate}
                      error={Boolean(errors.min_qty_trade_rate && touched.min_qty_trade_rate)}
                      sx={{ mb: 3 }}
                    />
                  </Grid>
                </Grid>
                <h4 style={{ color: theme.palette.primary.main, fontSize: '18px', }}>Tax Category</h4>
                <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                  <Grid item sm={12} xs={12} lg={8}>
                    <Box overflow="auto">
                      <TaxCategoryTable>
                        <TableHead>
                          <TableRow>
                            <TableCell></TableCell>
                            <TableCell>CGST</TableCell>
                            <TableCell>SGST</TableCell>
                            <TableCell>IGST</TableCell>
                            <TableCell>CESS</TableCell>
                          </TableRow>
                        </TableHead>
                        <TableBody>
                          {tax_categories && tax_categories.map((tax, index) => (
                            <TableRow key={index}>
                              <TableCell>
                                <Radio
                                  checked={values.tax_category_id == tax.id}
                                  onChange={handleChange}
                                  value={tax.id}
                                  name="tax_category_id"
                                  inputProps={{ 'aria-label': 'A' }}
                                />
                              </TableCell>
                              <TableCell>{tax.cgst}</TableCell>
                              <TableCell>{tax.sgst}</TableCell>
                              <TableCell>{tax.igst}</TableCell>
                              <TableCell>{tax.cess}</TableCell>
                            </TableRow>
                          ))}
                        </TableBody>
                      </TaxCategoryTable>
                      {(errors.tax_category_id)
                        && (touched.tax_category_id)
                        && <div style={{ color: '#ff3d57' }}>{`${errors.tax_category_id}`}</div>}
                    </Box>
                  </Grid>
                  <Grid item sm={12} xs={12} lg={4} style={{ textAlign: 'center' }}>
                    <label htmlFor="product_image" className='form-label'>Upload Image</label>
                    {values.product_image && (
                      <>
                        <br />
                        <br />
                        <img src={values.product_image} style={{ width: '300px', height: '200px' }} title="Product Image" />
                        <br />
                      </>
                    )}
                    <FormControl sx={{ mb: 3, float: 'right' }} >
                      <input
                        id="upload-photo"
                        name="product_image"
                        type="file"
                        onChange={changeHandler}
                      />
                    </FormControl>
                    {(errors.product_image)
                      && (touched.product_image)
                      && <div style={{ color: '#ff3d57' }}>{`${errors.product_image}`}</div>}
                  </Grid>
                </Grid>
                <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                  <Grid item sm={6} xs={12} lg={2}>
                    <LoadingButton
                      fullWidth
                      type="submit"
                      color="primary"
                      loading={loading}
                      variant="contained"
                      sx={{ mb: 2, mt: 3 }}
                    >
                      Update
                    </LoadingButton>
                  </Grid>
                </Grid>
              </form>
            )}
          </Formik>
        </CardContent>
      </Card>
    </Container >
  );
};

export default AddMedicinesStock;
